import axios from 'axios';

export const GET_POKEMONS = "GET_POKEMONS";


export const getPokemons = ()=>{
    return async  (dispatch)=>{
        try {
            let pedidoApi = await axios.get('http://localhost:3001/getAllPokemons');
            const pokemons = pedidoApi.data
             dispatch({
                type: 'GET_POKEMONS',
                payload: pokemons
            })   
        } catch (error) {
            dispatch({error: error.message})
        }
       
    };
}